import TextField from '@mui/material/TextField'
import styled, { css } from 'styled-components'
import Typography from '@mui/material/Typography'
import Button from '@mui/material/Button'
import { Box, Stack, Grid } from '@mui/material'

import dayjs, { Dayjs } from 'dayjs'

import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'
import { DatePicker } from '@mui/x-date-pickers/DatePicker'

import AddIcon from '@mui/icons-material/Add'

import React, { useState, useEffect, useRef } from 'react'

const LoginInput = styled(TextField)({
  margin: 5,
})

const CreateMeme = ({ addMeme }) => {
  const nameInput = useRef()
  const dateInput = useRef()
  const descriptionInput = useRef()

  const handleSubmit = (event) => {
    event.preventDefault()
    addMeme(
      nameInput.current.value,
      dateInput.current.value,
      descriptionInput.current.value
    )
    event.currentTarget.reset()
  }

  const [value, setValue] = React.useState(dayjs('2016-10-27'))

  const handleChange = (newValue) => {
    setValue(newValue)
  }

  return (
    <div className="login">
      <Box>
        <Grid
          container
          spacing={0}
          direction="column"
          alignItems="center"
          justifyContent="top"
          style={{ height: '100vh' }}
        >
          <form onSubmit={(event) => handleSubmit(event)}>
            <Stack direction="column" spacing={2} margin={5}>
              <Typography variant="h2" color="initial">
                Vytvořit mem
              </Typography>
              <LoginInput label="Název memu" inputRef={nameInput} />
              <LoginInput label="Vysvětlení" inputRef={descriptionInput} />
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DatePicker
                  disableFuture
                  label="Datum vzniku"
                  openTo="year"
                  views={['year', 'month']}
                  value={value}
                  inputRef={dateInput}
                  renderInput={(params) => <TextField {...params} />}
                />
              </LocalizationProvider>
              <Button
                variant="contained"
                color="info"
                type="submit"
                startIcon={<AddIcon />}
              >
                Vytvořit
              </Button>
            </Stack>
          </form>
          {/* {movieReviewList.map((val) => {
            return (
              <h1>
                Movie Name: {val.movieName} | Movie Review: {val.movieReview}
              </h1>
            )
          })} */}
        </Grid>
      </Box>
    </div>
  )
}

export default CreateMeme
